import React, { Component } from 'react';
import PortfolioItem from '../PortfolioItem/PortfolioItem.jsx'
import FirstScreen from '../FirstScreen/FirstScreen.jsx'
import WidItem from '../WidItem/WidItem.jsx'
import data from '../../data.json'

import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import { CSSTransitionGroup } from 'react-transition-group'
import Checkbox from 'material-ui/Checkbox';

import './App.sass';

const wid = [
	{
		icon: './img/react.png',
		title: 'React',
		description: 'developing SPA using <a href="https://reactjs.org/">ReactJS</a> famework, <a href="https://redux.js.org/">Redux</a> for managing application state, <a href="https://facebook.github.io/jest/">Jest</a> for unit tests'
	},
	{
		icon: './img/angular.png',
		title: 'Angular 2+',
		description: 'developing SPA using <a href="https://angular.io/">Angular</a> famework, <a href="http://reactivex.io/rxjs/">RxJS</a> for managing application state, <a href="https://jasmine.github.io/">Jasmine</a> for unit tests'
	},
	{
		icon: './img/web.png',
		title: 'HTML5+CSS3+JS',
		description: 'develop web sites using JQuery and many different libraries'
	},
]
export default class App extends Component {
	constructor(){
		super();
		this.state = {
			nofram: false,
			reactf: false,
			angularf: false
		}
	}

	handleCheck(){
		this.setState((state) => { nofram: !state.nofram });
	}

	render() {
		return (
			<MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
				<div>
					<FirstScreen />
					<div className="container">
						<div className="row">
							<div className="col-xs-12">
								<div className="wid-head">What I do</div>
							</div>
						</div>
					</div>
					<div className="container">
						<div className="row wid-items">
							{
								wid.map( (item,i) =>
									<WidItem
										key={i}
										icon={item.icon}
										title={item.title}
										description={item.description}
									/>
								)
							}
						</div>
					</div>
					<div className="portfolio-title">you can check examples of my work below:</div>
					<div className="container portfolio">
						<div className="row portfolio_items">
							{
								data.map( (item, i) =>
									<PortfolioItem
										key={i}
										title={item.title}
										description={item.description}
										link={item.link}
										pages={item.pages}
										preview={item.preview}
										backgroundColor={item.backgroundColor}
										galerylayoutFirst={item.galerylayoutFirst}
										galerylayout={item.galerylayout}
										rel={item.rel}
									/>
								)
							}
						</div>
					</div>
				</div>
			</MuiThemeProvider>
		);
	}
}



